html,
body {
  box-sizing: border-box;
  font-size: 16px;
  height: 100%;
  margin: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal; }

img {
  width: 100%;
  height: 100%; }

h1,
.main__h1--2p2Kt {
  font-size: 54px; }

h2,
.main__h2--2yq_v {
  font-size: 34px; }

h3,
.main__h3--1pFtO {
  font-size: 22px; }

h4,
.main__h4--1j4u2 {
  font-size: 20px; }

h5,
.main__h5--1h-5y {
  font-size: 16px; }

h6,
.main__h6--3VArs {
  font-size: 12px;
  text-transform: uppercase; }
